rict\">\n                            <select\n                                name=\"accdistrict\"\n                                id=\"accdistrict\"\n                                required\n                                v-model=\"search.city\"\n                            >\n                                <option\n                                    v-if=\"hideDefaultOption\"\n                                    disabled\n                                    selected\n                                >\n                                    Select State First\n                                </option>\n                                <option v-else disabled selected>\n                                    Select City\n                                </option>\n\n                                <option\n                                    v-for=\"(item, index) in district\"\n                                    :key=\"'N' + index\"\n                                    :value=\"item.id\"\n                                >\n                                    {{ item.name }}\n                                </option>\n                            </select>\n                        </label>\n                        <div>\n                            <date-picker v-model=\"range\" is-range>\n                                <template v-slot=\"{ inputValue, inputEvents }\">\n                                    <div\n                                        class=\"flex justify-center items-center date-pick-input\"\n                                    >\n                                        <span>\n                                            <img\n                                                src=\"/assets/vendors/accomodations/ant-design_calendar-outlined.svg\"\n                                                alt=\"\"\n                                                style=\"\n                                                    width: 18px;\n                             