                          @click=\"search.adult++\"\n                                            type=\"button\"\n                                        >\n                                            +\n                                        </button>\n                                    </span>\n                                </div>\n                                <div>\n                                    <span> Children </span>\n                                    <span>\n                                        <button\n                                            @click=\"search.children--\"\n                                            type=\"button\"\n                                        >\n                                            -\n                                        </button>\n                                        <input\n                                            type=\"number\"\n                                            v-model=\"search.children\"\n                                        />\n                                        <button\n                                            @click=\"search.children++\"\n                                            type=\"button\"\n                                        >\n                                            +\n                                        </button>\n                                    </span>\n                                </div>\n                                <div>\n                                    <span> Rooms </span>\n                                    <span>\n                                        <button\n                                            @click=\"search.rooms--\"\n                                            type=\"button\"\n                                        >\n                                            -\n                                        </button>\n                                        <input\n                                            type=\"number\"\n                                            v-model=\"search.rooms\"\n                                        />\n                                        <button\n                                            @click=\"search.rooms++\"\n                                            type=\"button\"\n                                        >\n                                            +\n                                        </button>\n                                    </span>\n                                </div>\n                            </div>\n                        </div>\n                        <button type=\"submit\">Search</button>\n                    </form>\n                </div>\n            </div>\n        </section>\n        <section class=\"container\">\n            <table class=\"vendor-add-room-table\">\n                <tr>\n                    <th style=\"width: 21%\">Room type</th>\n                    <th style=\"width: 9%\">Sleeps</th>\n                    <th style=\"width: 15%\">\n                        Price for {{ roomTableDetails.no_of_days }} days\n                    </th>\n                    <th style=\"width: 25%\">Facilities</th>\n                    <th style=\"width: 15%\">Rooms</th>\n                    <th style=\"width: 15%\">\n                        Price for {{ roomTableDetails.no_of_days }} days\n                    </th>\n                </tr>\n\n                <tr\n                    v-for=\"(item, index) in accomodationRomms\"\n                    :key=\"'O' + index\"\n                >\n                    <td style=\"width: 21%\">\n                        <div class=\"vendor-add-room-table-type\">\n                            <div>\n                                <u>{{ item.room_type }}</u>\n                            </div>\n                            <div>\n                                Only {{ item.total_rooms }} rooms left on our\n                                site\n                 