                               />\n                                </button>\n                            </div> -->\n                        </div>\n                    </td>\n                    <td style=\"width: 15%\">\n                        <div class=\"addRooms\">\n                            <div>\n                                <span v-if=\"roomTableDetails.room[index]\">\n                                    <button\n                                        @click=\"\n                                            roomTableDetails.room[index]\n                                                .roomRequired--;\n                                            setRoomDetails(false);\n                                        \"\n                                        type=\"button\"\n                                    >\n                                        -\n                                    </button>\n                                    <input\n                                        type=\"number\"\n                                        v-model=\"\n                                            roomTableDetails.room[index]\n                                                .roomRequired\n                                        \"\n                                    />\n                                    <button\n                                        @click=\"\n                                            roomTableDetails.room[index]\n                                                .roomRequired++;\n                                            setRoomDetails(false);\n                                        \"\n                                        type=\"button\"\n                                    >\n                                        +\n                                    </button>\n                                </span>\n                            </div>\n                        </div>\n                    </td>\n                    <td style=\"width: 15%\">\n                        <div class=\"vendor-add-room-table-enquiry\">\n                            <div>\n                                <b>{{\n                                    roomTableDetails.dispPrice.totalRooms\n                                }}</b>\n                                rooms for\n                            </div>\n                            <div>\n                                ₹\n                                {{\n                                    roomTableDetails.dispPrice.price *\n                                    roomTableDetails.no_of_days\n                                }}\n                            </div>\n                            <div>\n                                ₹\n                                {{\n                                    roomTableDetails.dispPrice.tax *\n                                    roomTableDetails.no_of_days\n                                }}\n                                taxes and charges\n                            </div>\n                            <button @click=\"hidden = true\" type=\"button\">\n                                Enquiry\n                            </button>\n                            <ul>\n                                <li>Confirmation is immediate</li>\n                                <li>No registration required</li>\n                                <li>No booking or credit card fees!</li>\n                            </ul>\n                        </div>\n                    </td>\n                </tr>\n            </table>\n            <div\n                v-show=\"hidden\"\n                style=\"\n                    z-index: 9999999;\n                    width: 100%;\n                    height: 100vh;\n                    position: fixed;\n                    top: 0;\n                    left: 0;\n                    background: rgba(0, 0, 0, 0.7);\n                \"\n            >\n                <form\n                    @submit.preve